.card {
  background: red;
  border-radius: 12px;
  color: aqua;
}

button, .btn {
  text-decoration: none;
  border-radius: 8px;
  padding: 0.65em 1.5em;
  display: inline-block;
  text-align: center;
  max-width: 150px;
  border: none;
  transition: all 0.2s linear;
  font-size: 1rem;
}
button:hover, .btn:hover {
  cursor: pointer;
}
button[type=submit], [type=submit].btn {
  background: linear-gradient(to bottom right, #ed1c24, #5c0000);
  color: #fff;
  box-shadow: 0 0 7px -1px #000;
}
button[type=submit]:hover, [type=submit].btn:hover {
  transform: scale(1.15);
}
button[type=button], [type=button].btn {
  background: linear-gradient(to bottom right, #64b7da, #20303B);
  color: #fff;
  box-shadow: 0 0 7px -1px #000;
}
button[type=button]:not([disabled]):hover, [type=button].btn:not([disabled]):hover {
  transform: scale(1.15);
}
button[disabled], [disabled].btn {
  box-shadow: inset 0 0 0 2px #838383, 0 0 7px -1px #000;
  color: #838383;
  background: #cfcfcf;
  cursor: not-allowed;
}

.cta-1 {
  background: linear-gradient(to bottom right, #ed1c24, #5c0000);
  color: #fff;
  box-shadow: 0 0 7px -1px #000;
}
.cta-1:hover {
  transform: scale(1.15);
}
.cta-2 {
  background: linear-gradient(to bottom right, #64b7da, #20303B);
  color: #fff;
  box-shadow: 0 0 7px -1px #000;
}
.cta-2:hover {
  transform: scale(1.15);
}
.cta-3 {
  background: linear-gradient(to bottom right, #fff, #b3b3b3);
  color: #20303B;
  box-shadow: inset 0 0 0 2px #20303B, 0 0 7px -1px #000;
}
.cta-3:hover {
  transform: scale(1.15);
}

html,
body {
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-align: center;
}

section > h2:first-child {
  margin-top: 0;
  width: 100%;
}

section {
  padding: 30px 20% 44px;
  text-align: center;
}
section:nth-child(even) {
  background: #e0e0e0;
}

a.sg-no-style {
  all: revert;
}

#colors {
  text-align: center;
  padding: 30px 20%;
}
#colors h4 {
  margin: 0 0 10px 0;
  padding: 0;
}

.color-palette {
  display: flex;
  justify-content: space-evenly;
}

.color-swatch {
  display: flex;
  flex-direction: column;
  background: #fff;
  padding: 10px;
  border: 1px solid #000;
}

.swatch-name::before {
  content: "";
  height: 80px;
  width: 130px;
  display: block;
  background: lime;
  border: 1px solid #000;
}

.sec-swatch .swatch-name::before {
  background: #9d00ff;
}

#buttons > p {
  max-width: 550px;
  margin: 0 auto 30px;
}
#buttons button,
#buttons .btn {
  margin: 10px 15px;
}

#typography {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
#typography h2 {
  width: 100%;
}
#typography .font-sampler {
  text-align: center;
}

#text-content {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 10%;
  justify-content: center;
  gap: 30px;
}
#text-content h2 {
  width: 100%;
}
#text-content div {
  background: lightgreen;
}
#text-content #headings {
  width: 200px;
}
#text-content #common-text-elements {
  width: 800px;
  display: flex;
}
#text-content #common-text-elements div:first-child {
  width: 30%;
}
#text-content #common-text-elements div:last-child {
  width: 70%;
}
#text-content #misc-text-elements {
  width: 400px;
}
#text-content #in-out-elements {
  display: none;
}
#text-content #paragraphs {
  width: calc(50% - 15px);
}
#text-content #lists {
  display: flex;
  width: calc(50% - 15px);
}

#media {
  display: flex;
  flex-wrap: wrap;
}
#media div {
  width: 30%;
  background: lightcoral;
  display: flex;
  justify-content: center;
  align-items: center;
}

